.about {
  padding: 30px;
  display: grid;
  grid-template-columns: 1.7fr 1.3fr;

  .avatar img {
    width: 512px;
    height: auto;
    border-radius: 10px;
  }

  .skills {
    margin-top: 10px;

    ul {
      margin-top: 3%;
      margin-left: 7%;
      font-size: 20px;

      li span {
        font-family: "Courier New", sans-serif;
      }

      li img {
        width: 25px;
        height: auto;
      }
    }

    .skills_list,
    .skills_list li {
      list-style: disc;
    }

    .about_me_list {
      margin-bottom: 5%;
    }

    .about_me_list,
    .about_me_list li {
      list-style-type: "-";
    }
  }

  // Адаптивность для планшетов
  @media (max-width: 1024px) {
    grid-template-columns: 1fr;

    .avatar img {
      width: 80%;
      margin: 0 auto;
    }

    .skills {
      ul {
        font-size: 18px;
        margin-left: 5%;
      }
    }
  }

  // Адаптивность для мобильных устройств
  @media (max-width: 768px) {
    padding: 20px;
    grid-template-columns: 1fr;

    .avatar img {
      width: 70%;
      margin: 0 auto;
    }

    .skills {
      ul {
        font-size: 16px;
        margin-left: 3%;
      }

      .skills_list li img {
        width: 20px;
      }
    }
  }

  // Адаптивность для маленьких мобильных устройств
  @media (max-width: 480px) {
    padding: 15px;
    grid-template-columns: 1fr;

    .avatar img {
      width: 100%;
      margin: 0 auto;
    }

    .skills {
      ul {
        font-size: 14px;
        margin-left: 2%;
      }

      .skills_list li img {
        width: 18px;
      }
    }
  }
}
