.main_header {
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .header_content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;

    .logo {
      font-size: 24px;
    }

    .hamburger {
      display: none;
      background: none;
      border: none;
      font-size: 24px;
      cursor: pointer;
    }
  }

  .intro {
    flex-wrap: wrap;
    width: 50%;
    font-size: 30px;
    transition: all 0.3s;

    &.hide {
      display: none;
    }

    &.show {
      display: block;
    }
  }

  // Адаптивность для мобильных устройств
  @media (max-width: 768px) {
    height: auto;

    .header_content {
      justify-content: space-between;

      .hamburger {
        display: block; // Показать кнопку гамбургера на мобильных устройствах
      }
    }

    .intro {
      width: 90%;
      font-size: 20px;
      text-align: center;

      &.hide {
        display: none; // Скрывать текст по умолчанию на мобильных
      }

      &.show {
        display: block; // Показывать текст при нажатии на кнопку
      }
    }
  }
}
