.nav {
  padding: 25px;
  font-size: 20px;

  .logo_container {
    width: fit-content;
    margin-left: 5%;
    transition: all 0.3s;
    position: relative;
    display: inline-block;
  }

  .logo_link {
    color: $text-color-light;
    width: fit-content;
  }

  .logo_link:hover {
    background-color: rgba(255, 255, 255, 0);
  }

  .logo_container::before {
    content: "";
    position: absolute;
    width: 100px;
    height: 10px;
    background-color: $secondary-color-light;
    border-radius: 50%;
    transform: translate(-5%, 100%) scale(0);
    transition: transform 0.5s ease;
    z-index: -1;
  }

  .logo_container:hover::before {
    transform: translate(0%, 100%) scale(1);
  }

  .logo {
    width: fit-content;
    display: block;
    position: relative;
    transition: transform 0.5s ease;
    z-index: 1;
  }

  .logo:hover {
    transform: translateY(-5px);
  }

  .nav_list_item {
    min-width: fit-content;
  }

  .nav_list_item_active {
    text-decoration: underline;
    text-underline-offset: 10px;
  }

  // Адаптивность для мобильных устройств
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: center;

    .logo_container {
      margin-left: 0;
      margin-bottom: 10px;
    }

    .container {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;

      ul {
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-top: 10px;
      }

      .nav_list_item {
        margin-bottom: 10px;
      }
    }

    .theme_btn {
      margin-top: 10px;
    }
  }
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-direction: row;

  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  // Адаптивность для мобильных устройств
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.theme_btn {
  order: 9;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 51px;
  height: 27px;
  padding: 5px;
  border-radius: 50px;

  img {
    width: 40%;
    height: auto;
  }

  // Адаптивность для мобильных устройств
  @media (max-width: 768px) {
    width: 45px;
    height: 25px;
  }
}

.theme_btn::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #fff;
  transition: left 0.2s ease-in;
}

.theme_btn-active::before {
  left: 24px;
}

.dark_theme_btn_icon {
  position: relative;
  z-index: 9;
}
