.contacts-links-block {
    a {
      display: inline-block;
    }
  
    img {
      width: 50px;
      height: auto;
    }
  
    display: flex;
    width: 100%;
    height: 50vh;
    align-items: center;
    justify-content: center;
  
    ul {
      width: 100%;
      display: flex;
      justify-content: space-around;
  
      li {
        transition: all 0.5s;
      }
  
      li:hover {
        transform: scale(1.2);
      }
    }
  
    // Адаптивность для планшетов
    @media (max-width: 1024px) {
      ul {
        flex-direction: column;
        align-items: center;
        li {
          margin-bottom: 15px;
        }
      }
  
      img {
        width: 40px;
      }
    }
  
    // Адаптивность для мобильных устройств
    @media (max-width: 768px) {
      height: auto;
      ul {
        flex-direction: column;
        gap: 10px;
      }
  
      img {
        width: 35px;
      }
    }
  
    // Адаптивность для маленьких мобильных устройств
    @media (max-width: 480px) {
      ul {
        width: 90%;
        li {
          margin-bottom: 10px;
        }
      }
  
      img {
        width: 30px;
      }
    }
  }
  
  .phone {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    input {
      border: 1px solid black;
      width: 11%;
      padding: 5px;
      border-radius: 10px;
      margin-top: 0.5%;
    }
  
    // Адаптивность для планшетов
    @media (max-width: 1024px) {
      input {
        width: 20%;
      }
    }
  
    // Адаптивность для мобильных устройств
    @media (max-width: 768px) {
      input {
        width: 40%;
      }
    }
  
    // Адаптивность для маленьких мобильных устройств
    @media (max-width: 480px) {
      input {
        width: 80%;
      }
    }
  }
  